.usersPage{
  width: 100%;
  padding: 60px;
  position: relative;
}
.filterBox{
  margin-bottom: 12px;
}


.addUserBox{
  margin-bottom: 24px;
}
.users{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.usersBox{
  width: 30%;  
}

.profileBox{
  position: relative;
  width: 65%;
}

.loading {
  position: absolute;
  top:0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #fff;
  opacity: 0.5;
}


@media(max-width:1200px) {
  .usersBox{ width: 100%;  }
  .profileBox{ width: 100%;  }
}

@media(max-width:992px) {
  .usersPage {padding: 20px;}
  
}