.title{
    font-size: 18px;
    margin: 0;
    font-weight: bold;
}
.profileItem{
    display: block;
    padding: 10px;
    margin-bottom: 12px;
    border-radius: 8px;
    background-color:#e3f2fd;

}

.close {
    float: right;
    cursor: pointer;
    text-decoration: underline;
    
}

.clearIcon{
    float: right;
    cursor: pointer;
    padding: 8px;
}