.task {
    width: 100%;
    display: inline-flex;
    align-items:center;
    justify-content: space-between;
    position: relative;
}

.title {
    width: 110px;
    font-size: 1rem;
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    font-weight: 500;
    line-height: 1.5;
    letter-spacing: 0.00938em;
}

.priority {
    width: 160px;
    cursor: pointer;
    padding: 3px 5px;
    font-size: 1rem;
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    font-weight: 400;
    line-height: 1.5;
    letter-spacing: 0.00938em;
}

.priorityColor{
    font-size: 14px;
    font-weight: bold;
   padding: 4px 10px;
}
.low{
background-color: #8bc34a;
}

.medium{
    background-color: #ffeb3b;

}

.high{
    background-color: #ff5722;
}

.del{
    /* position: absolute;
    top:12px;
    right: -3px; */
    cursor: pointer;
}

.del:hover {
    color: #ff5722;
}

.completedTask{
    opacity: 0.4;
}
